#markAsOrigin{
   top: 365px;
   right: 20px;
}
.tool.markAsOrigin-1 {
  background: url('icons/markasorigin.png') center center no-repeat;  
  background-size:35px;
}
.markasorigin-tool
{
margin:0px 1px;
}
#markasorigin.modal-body{
padding:5px 0px 0px 5px !important;
}
#markasorigin.modal-dialog
{
top: 368px;
right: 70px;
}
.tool.markAsOrigin-1 .form_body:before
{    content: '';
    width: 0;
    height: 0;
    position: absolute;
    right: -11px;
    top: 50%;
    border-left: 11px solid #a2a2a2;
    border-top: 11px solid transparent;
    border-bottom: 11px solid transparent;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    z-index: 9;
}
.tool.markAsOrigin-1 .modal-body.form_body:after 
{
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    right: -10px;
    top: 50%;
    border-left: 10px solid #ffffff;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    z-index: 99;
}

